<template>
  <main>
    <form @submit.prevent="handleSubmit" method="post" class="signup">
      <div class="signup-container">
        <h1>회원가입</h1>
        <p>회원이 되어 다양한 헤택을 경험해 보세요!</p>
        <div class="signup-id input-block">
          <label class="signup-label" for="id">아이디
            <span class="available" v-if="idAvailable === true">사용 가능한 아이디입니다.</span>
            <span class="notavailable" v-else-if="idAvailable === false">
              이미 사용 중인 아이디입니다.
            </span>
          </label>
          <div class="signup-field">
            <input type="text" id="id" name="id" placeholder="아이디를 입력하세요." class="signup-input"
              v-model="formData.loginId" />
            <button type="button" class="signup-btn" @click="checkId">
              중복 확인
            </button>
          </div>
        </div>
        <div class="signup-pw input-block">
          <label class="signup-label" for="password">비밀번호</label>
          <div class="signup-field">
            <input type="password" id="password" name="password" placeholder="비밀번호를 입력하세요." class="signup-input"
              v-model="formData.password" />
          </div>
        </div>
        <div class="signup-pw-check input-block">
          <label class="signup-label" for="password-check">비밀번호 확인
            <!-- 일치하면 메세지 -->
            <span v-if="Bothpasswords && passwordsMatch" class="password-match">일치합니다</span>
            <!-- 불일치하면 메세지 -->
            <span v-else-if="Bothpasswords && !passwordsMatch" class="password-mismatch">일치하지 않습니다</span>
          </label>
          <div class="signup-field">
            <input type="password" id="password-check" name="passwordcheck" placeholder="비밀번호 재입력" class="signup-input"
              v-model="passwordCheck" />
          </div>
        </div>
        <div class="signup-name input-block">
          <label class="signup-label" for="name">이름</label>
          <div class="signup-field">
            <input type="text" id="name" name="name" placeholder="이름을 입력해주세요." class="signup-input"
              v-model="formData.username" />
          </div>
        </div>
        <div class="signup-phone input-block">
          <label class="signup-label" for="phone">전화번호</label>
          <div class="signup-field">
            <input type="text" id="phone" name="phone" placeholder="전화번호를 입력해주세요." class="signup-input"
              v-model="formData.phoneNumber" />
          </div>
        </div>

        <div class="signup-email input-block">
          <label class="signup-label" for="email">이메일</label>
          <div class="signup-field">
            <input type="text" id="email" name="email" placeholder="이메일을 입력해주세요." class="signup-input"
            v-model="emailValue" />
            <button type="button" class="signup-btn" @click="sendEmailRequest" :disabled="timerStarted">
              인증 요청
            </button>
            <div v-if="loading" class="spinner"></div>
          </div>
        </div>

        <div class="signup-email-auth input-block">
          <label class="signup-label" for="email-number">이메일 인증번호
            <span v-if="timerStarted && timeLeft > 0" class="timer">{{ minutes }}:{{ seconds }}</span>
            <button type="button" v-if="!timerStarted && timeLeft === 0" class="re-request-btn"
              @click="handleReRequest">
              재전송
            </button>
          </label>
          <div class="signup-field">
            <input type="text" id="email-number" name="email-number" placeholder="인증번호를 입력해주세요." class="signup-input"
              v-model="enteredCode" />
            <button type="button" class="signup-btn" @click="updateEmailAfterCheck">
              확인
            </button>
          </div>
        </div>
        <div class="signup-terms">
          <label class="signup-terms-label">
            <input type="checkbox" name="terms" value="agree" v-model="agreeAll" @click="selectAll" />
            <span class="signup-terms-text">이용약관 전체 동의<span @click="goTos">&#62;</span></span>
          </label>
          <label class="signup-terms-label">
            <input type="checkbox" name="terms1" value="agree-1" id="agree01" v-model="agree01" />
            <span class="signup-terms-text">이용약관 동의(필수)<span @click="goTos">&#62;</span></span>
          </label>
          <label class="signup-terms-label">
            <input type="checkbox" name="terms2" value="agree-2" id="agree02" v-model="agree02" />
            <span class="signup-terms-text">개인정보 수집 및 이용 동의(필수)<span @click="goTos">&#62;</span></span>
          </label>
          <label class="signup-terms-label">
            <input type="checkbox" name="terms3" value="agree-3" id="agree03" v-model="agree03" />
            <span class="signup-terms-text">프로모션 정보 수신 동의(선택)<span @click="goTos">&#62;</span></span>
          </label>
        </div>
        <button class="signup-terms-btn" type="button" @click="account">
          회원가입
        </button>
        <p class="signup-account-check">
          이미 계정이 있으신가요?<span class="signup-account-check-text" @click="goSignIn">로그인 하기</span>
        </p>
      </div>
    </form>
  </main>
</template>

<script>
import { ref, computed, onMounted, watch } from "vue";
import { useRouter, useRoute } from "vue-router";
import { useEmail } from "@/services/sendEmail";
import axios from "axios";
import { toRaw } from "vue";

export default {
  name: "SignUp",

  setup() {
    const router = useRouter();

    const formData = ref({
      loginId: "",
      password: "",
      username: "",
      phoneNumber: "",
      email: "",
    });
    const passwordCheck = ref("");
    const enteredCode = ref("");
    const emailValue = ref("");

    onMounted(() => {
      const storedData = JSON.parse(localStorage.getItem('signupData')) || {};
      formData.value = { ...formData.value, ...storedData };
      passwordCheck.value = storedData.passwordCheck || "";
      emailValue.value = storedData.emailValue || "";
      enteredCode.value = storedData.enteredCode || "";

      window.addEventListener('beforeunload', () => {
        localStorage.removeItem('signupData');
      });
    });

    watch([formData, passwordCheck, enteredCode], ([newFormData, newPasswordCheck, newEmailValue, newEnteredCode]) => {
      localStorage.setItem('signupData', JSON.stringify({
        ...newFormData,
        passwordCheck: newPasswordCheck,
        emailValue: newEmailValue,
        enteredCode: newEnteredCode,
      }));
    }, { deep: true });

    watch(router.currentRoute, (to) => {
      if (to.path === '/sign-up') {
        const FromTerms = router.currentRoute.value.path === '/terms-of-service';
        if (!FromTerms) {
          localStorage.removeItem('signupData');
          formData.value = {
            loginId: "",
            password: "",
            username: "",
            phoneNumber: "",
            email: "",
          };
          passwordCheck.value = "";
          emailValue.value = "";
          enteredCode.value = "";
        }
      }
    });

    // formData에 값 콘솔에서 볼려고 작성함
    console.log(toRaw(formData.value));
    // 아이디 사용 가능 여부 저장 상태

    const passwordsMatch = computed(() => {
      return formData.value.password === passwordCheck.value;
    })
    // 패스워드 입력이 끝난 후 메시지 표시
    const Bothpasswords = computed(
      () => formData.value.password && passwordCheck.value
    );

    const idAvailable = ref(null);
    // 아이디 중복 확인 요청
    const checkId = async () => {
      try {
        const response = await axios.post("/api/check-id", {
          loginId: formData.value.loginId,
        });

        if (response.data === "사용가능한 아이디입니다.") {
          idAvailable.value = true;
          alert("아이디 사용 가능합니다.");
        } else if (response.data === "존재하는 아이디입니다.") {
          idAvailable.value = false;
          alert("아이디가 이미 존재합니다.");
        } else {
          idAvailable.value = false;
          alert("오류");
        }
      } catch (error) {
        console.error(error);
        alert("아이디 중복 확인 중 오류가 발생했습니다.");
      }
    };

    //  이메일 전송
    const sendEmailRequest = async () => {
      try {
        await sendEmail(emailValue.value);
      } catch (error) {
        alert("이메일 전송에 실패했습니다.");
      }
    };
    //  이메일 인증
    const updateEmailAfterCheck = async () => {
      try {
        const isVerified = await verifyEmail(emailValue.value, enteredCode.value);
        console.log("인증 결과: ", isVerified);
        if (isVerified) {
          formData.value.email = emailValue.value; // 인증 완료 후 email을 formData에 업데이트
          alert("인증 완료");
        } else {
          alert("인증 번호가 만료되었거나, 일치하지 않습니다.");
        }
      } catch (error) {
        console.error("이메일 인증 오류: ", error);
      }
    };

    console.log(toRaw(formData));

    // 회원가입 버튼 클릭 시 사용자 데이터(formData) 서버에 보냄
    const account = async () => {

      if (!passwordsMatch.value) {
        alert("비밀번호가 불일치 합니다.");
        return;
      }
      if (!idAvailable.value) {
        alert("아이디 중복 확인 해주세요.");
        return;
      }
      if (formData.value.username === null) {
        alert("이름을 입력해주세요.");
        return;
      }
      if (formData.value.phoneNumber === null) {
        alert("전화번호를 입력해주세요.");
        return;
      }

      try {
        await axios.post("/api/user-account", formData.value);
        alert("회원가입 성공!");

        localStorage.removeItem('signupData');

        router.push({ name: "SignIn" });
      } catch (error) {
        console.log("회원가입 시 서버와 통신 에러");
      }
    };

    const goTos = () => {
      router.push({ name: "TermsOfService" });
    };

    const goSignIn = () => {
      router.push({ name: "SignIn" });
    };

    const selectAll = () => {
      const allSelected = agree01.value && agree02.value && agree03.value;
      agreeAll.value = !allSelected;
      agree01.value = !allSelected;
      agree02.value = !allSelected;
      agree03.value = !allSelected;
    };

    // tos에서 선택한 동의 선택 데이터 가져오기
    const route = useRoute();
    const query = route.query;

    const agree01 = ref(query.agree01 === "true");
    const agree02 = ref(query.agree02 === "true");
    const agree03 = ref(query.agree03 === "true");
    const agreeAll = ref(query.agreeAll === "true");

    const {
      sendEmail,
      verifyEmail,
      timeLeft,
      minutes,
      seconds,
      handleReRequest,
      timerStarted,
      loading,
    } = useEmail();

    return {
      goTos,
      goSignIn,
      account,

      agreeAll,
      agree01,
      agree02,
      agree03,
      selectAll,

      updateEmailAfterCheck,

      timeLeft,
      minutes,
      seconds,
      timerStarted,
      emailValue,
      enteredCode,
      sendEmail,
      verifyEmail,
      handleReRequest,
      sendEmailRequest,
      loading,

      formData,
      idAvailable,
      checkId,
      passwordCheck,
      passwordsMatch,
      Bothpasswords,
    };
  },
};
</script>

<style scoped>
.spinner {
  border: 4px solid black;
  border-radius: 50%;
  border-top: 4px solid #ccc;
  width: 24px;
  height: 24px;
  animation: spin 1s linear infinite;
  margin-top: 10px;
  margin-left: 10px;
}

/* 애니메이션 */
@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.timer {
  color: red;
  font-size: 14px;
  margin-left: 170px;
}

.re-request-btn {
  margin-left: 170px;
  border: none;
  background-color: white;
  color: blue;
}

.password-match {
  margin-left: 225px;
  font-size: 14px;
  color: blue;
}

.password-mismatch {
  margin-left: 180px;
  font-size: 14px;
  color: red;
}

main {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 1400px;
}

.signup {
  margin-top: 50px;
}

.signup-container {
  width: 490px;
  /* height: 1190px; */
  height: 1250px;
  text-align: center;
  display: flex;
  flex-direction: column;
  border: 1px solid rgba(0, 0, 0, 0.164);
  /* margin-bottom: 100px; */
}

.signup-container h1 {
  margin: 50px 0px;
}

/* .signup-container p {
  margin-top: -20px;
  margin-bottom: 30px;
} */

.signup-desc {
  margin-top: -20px;
  margin-bottom: 30px;
}

.input-block {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 100%;
}

.signup-label {
  margin-bottom: 10px;
  font-weight: bold;
}

/* 아이디 중복 확인 메시지 위치 */
.available {
  font-size: 14px;
  margin-left: 84px;
}

.notavailable {
  margin-left: 66px;
  font-size: 14px;
}

.signup-field {
  display: flex;
  width: 404px;
  height: 40x;
}

.signup-input {
  flex: 1;
  padding: 5px 0px 5px 10px;
  border: 1px solid rgba(0, 0, 0, 0.253);
  border-radius: 0.1875rem;
  box-sizing: border-box;
  width: 100px;
  height: 40px;
  outline: none;
}

.signup-input:hover {
  border: 1px solid black;
}

.signup-btn {
  padding: 8px 16px;
  margin-left: 10px;
  font-weight: bold;
  border: none;
  background-color: #007bff;
  color: white;
  border-radius: 0.1875rem;
  cursor: pointer;
}

.signup-id,
.signup-pw,
.signup-pw-check,
.signup-name,
.signup-phone,
.signup-email,
.signup-email-auth,
.signup-account-check {
  margin: 30px 0px 0px 40px;
}

input::placeholder {
  font-size: small;
}

.at {
  margin: 8px 0px 5px 10px;
}

.signup-email-domain {
  margin-left: 10px;
  padding: 8px;
  border: 1px solid rgba(0, 0, 0, 0.253);
  background-color: white !important;
  border-radius: 10px;
  font-size: 13.3333px;
  color: rgba(0, 0, 0, 0.616);
  outline: none;
}

.signup-terms {
  width: 380px;
  text-align: left;
  margin-top: 10px;
  margin: 30px 0px 0px 40px;
}

.signup-terms-label {
  display: block;
  margin-top: 20px;
  font-weight: bold;
  width: 400px;
}

.signup-terms-text span {
  font-size: 14px;
  font-weight: bold;
  float: right;
  cursor: pointer;
}

.signup-terms-text {
  margin-left: 7px;
  font-size: 13px;
}

.signup-terms-btn {
  width: 200px;
  height: 40px;
  border: none;
  border-radius: 0.1875rem;
  background-color: #007bff;
  color: white;
  cursor: pointer;
  font-size: 20px;
  font-weight: bold;
  margin: 50px 0px -10px 143px;
}

.signup-account-check-text {
  color: #007bff;
  margin-left: 20px;
  margin-right: 45px;
  cursor: pointer;
}
</style>
