<template>
  <main>
    <div class="finder-box">
      <h1>비밀번호 변경</h1>
      <span class="finder-text">비밀번호를 변경하시겠습니까?</span>
      <form action="#" class="finder-form">
        <div class="left-info">
          <div class="info-title">ID</div>
          <div class="profile-frame">
            <span id="id">{{ loginId }}</span>
          </div>
        </div>
        <div class="left-info">
          <div class="info-title">이메일</div>
          <div class="profile-frame">
            <span id="email">{{ email }}</span>
          </div>
          <button
            class="finder-field-btn"
            type="button"
            @click="sendEmailRequest"
          >
            인증 요청
          </button>
          <div v-if="loading" class="spinner"></div>
        </div>
        <div class="finder-email-auth input-block">
          <label class="finder-label" for="email-auth"
            >이메일 인증번호
            <span v-if="timerStarted && timeLeft > 0" class="timer"
              >{{ minutes }}:{{ seconds }}</span
            >
            <button
              v-if="!timerStarted && timeLeft === 0"
              class="re-request-btn"
              @click="handleReRequest"
            >
              재전송
            </button>
          </label>
          <div class="finder-field">
            <input
              type="text"
              id="email-auth"
              name="email-auth"
              placeholder="인증번호를 입력해주세요."
              class="finder-field-input"
              v-model="enteredCode"
            />
            <button
              class="finder-field-btn"
              type="button"
              @click="updateEmailAfterCheck"
            >
              확인
            </button>
          </div>
        </div>
        <div class="finder-pw input-block">
          <label class="finder-label" for="current-password"
            >현재 비밀번호</label
          >
          <div class="finder-field">
            <input
              type="password"
              id="current-password"
              name="current-password"
              placeholder="현재 비밀번호를 입력하세요."
              class="finder-field-input"
              v-model="currentPassword"
            />
          </div>
        </div>
        <div class="finder-pw input-block">
          <label class="finder-label" for="password">새 비밀번호</label>
          <div class="finder-field">
            <input
              type="password"
              id="password"
              name="password"
              placeholder="새 비밀번호를 입력하세요."
              class="finder-field-input"
              v-model="password"
            />
          </div>
        </div>
        <div class="finder-pw-check input-block">
          <label class="finder-label" for="pw-check"
            >새 비밀번호 확인
            <span v-if="Bothpasswords && passwordMatch" class="password-same"
              >일치합니다.</span
            >
            <span
              v-else-if="Bothpasswords && !passwordMatch"
              class="password-different"
              >일치하지 않습니다.</span
            >
          </label>
          <div class="finder-field">
            <input
              type="password"
              id="password-check"
              name="pw-check"
              placeholder="새 비밀번호 재입력"
              class="finder-field-input"
              v-model="passwordCheck"
            />
          </div>
        </div>
        <button type="button" class="finder-pw-btn" @click="changePassword">
          비밀번호 변경완료
        </button>
      </form>
    </div>
  </main>
</template>

<script>
import { ref, computed, onMounted } from "vue";
import { useRouter } from "vue-router";
import { useStore } from "vuex";
import apiClient from "@/api/apiClient";
import { useEmail } from "@/services/sendEmail";

export default {
  name: "ChangePassword",

  setup() {
    const router = useRouter();
    const store = useStore();

    // 반응형 데이터 정의
    const loginId = ref("");
    const email = ref("");
    const currentPassword = ref("");
    const password = ref("");
    const passwordCheck = ref("");
    const enteredCode = ref("");
    const isEmailSent = ref(false);
    const isEmailVerified = ref(false);

    // 이메일 관련 기능 가져오기
    const {
      sendEmailToken,
      verifyEmailToken,
      timeLeft,
      minutes,
      seconds,
      handleReRequest,
      timerStarted,
      loading,
    } = useEmail();

    // 사용자 프로필 정보 가져오기
    const fetchUserProfile = async () => {
      try {
        const response = await apiClient.get("/profile");
        loginId.value = response.data.loginId;
        email.value = response.data.email;
      } catch (error) {
        console.error("Error fetching user profile:", error);
      }
    };

    // 컴포넌트가 마운트될 때 프로필 정보를 가져옴
    onMounted(() => {
      fetchUserProfile();
    });

    // 마이페이지에서 비밀번호 변경 시 - 이메일 인증 요청 함수
    const sendEmailRequest = async () => {
      try {
        await sendEmailToken(email.value);
        isEmailSent.value = true;
      } catch (error) {
        alert("이메일 전송에 실패했습니다.");
      }
    };

    // 마이페이지에서 비밀번호 변경 시 - 이메일 인증 코드 확인 함수
    const updateEmailAfterCheck = async () => {
      try {
        const isEmailVerified = await verifyEmailToken(email.value, enteredCode.value);
        if (isEmailVerified) {
          alert("이메일 인증 완료");
        } else {
          alert("이메일 인증번호가 틀립니다.");
        } 
      } catch (error) {
        alert("이메일 인증 서버 통신 실패");
      }
    };

    // 비밀번호 일치 여부 확인
    const passwordMatch = computed(() => {
      return password.value === passwordCheck.value;
    });

    const Bothpasswords = computed(() => {
      return password.value && passwordCheck.value;
    });

    // 비밀번호 변경 함수
    const changePassword = async () => {
      // if (!isEmailVerified.value) {
      //   alert("이메일 인증을 완료해주세요.");
      //   return;
      // }

      // if (!currentPassword.value) {
      //   alert("현재 비밀번호를 입력해주세요.");
      //   return;
      // }
      // if (!passwordMatch.value) {
      //   alert("새 비밀번호 확인이 일치하지 않습니다.");
      //   return;
      // }

      try {
        const response = await apiClient.post("/user/password/change", {
          loginId: loginId.value,
          email: email.value,
          currentPassword: currentPassword.value,
          password: password.value,
          passwordCheck: passwordCheck.value,
        });

        if (response.status === 200) {
          alert(response.data.message);

          const success = await store.dispatch('isLogged/logout');
          if (success) {
            alert("재로그인 해주세요.")
            router.push({ name: "SignIn" });
          }

        } else {
          alert("비밀번호 변경에 실패했습니다.");
        }
      } catch (error) {
        console.error("서버와의 통신 오류 발생", error);
        alert(error.response?.data || "비밀번호 변경 중 오류가 발생했습니다.");
      }
    };

    return {
      loginId,
      email,
      currentPassword,
      password,
      passwordCheck,
      enteredCode,
      isEmailVerified,
      sendEmailRequest,
      updateEmailAfterCheck,
      changePassword,
      timeLeft,
      minutes,
      seconds,
      timerStarted,
      handleReRequest,
      passwordMatch,
      Bothpasswords,
      isEmailSent,
      loading,
    };
  },
};
</script>



<style scoped>
.spinner {
  border: 4px solid black;
  border-radius: 50%;
  border-top: 4px solid #ccc;
  width: 24px;
  height: 24px;
  animation: spin 1s linear infinite;
  margin-top: 10px;
  margin-left: 10px;
}

/* 애니메이션 */
@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.same-block {
  display: flex;
}

.password-same {
  margin-left: 194px;
  font-size: 14px;
  color: blue;
}

.password-different {
  margin-left: 148px;
  font-size: 14px;
  color: red;
}

.timer {
  color: red;
  font-size: 14px;
  margin-left: 170px;
}

.re-request-btn {
  margin-left: 170px;
  border: none;
  background-color: white;
  color: blue;
}

main {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.finder-box {
  width: 500px;
  padding: 20px;
  border-radius: 4px;
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 1px solid rgba(0, 0, 0, 0.164);
  background-color: #f9f9f9;
}

h1 {
  margin-bottom: 20px;
  font-size: 30px;
}

.finder-text {
  margin-bottom: 20px;
  font-size: 15px;
  color: grey;
}

.input-block {
  width: 100%;
  margin: 15px 0;
}

.finder-label {
  margin-bottom: 5px;
  font-weight: bold;
  font-size: 14px;
}

.finder-field {
  display: flex;
  align-items: center;
  width: 100%;
}

.finder-field-input {
  flex: 1;
  padding: 8px;
  border: 1px solid rgba(0, 0, 0, 0.253);
  border-radius: 0.1875rem;
  box-sizing: border-box;
  height: 40px;
  outline: none;
}

.finder-field-input:hover {
  border: 1px solid black;
}

.finder-field-btn {
  padding: 8px 16px;
  border: none;
  background-color: #007bff;
  color: white;
  border-radius: 0.1875rem;
  margin-left: 10px;
  font-size: 14px;
  cursor: pointer;
  font-weight: bold;
}

input::placeholder {
  font-size: small;
}

.finder-field-domain {
  margin-right: 10px;
  padding: 8px;
  border: 1px solid rgba(0, 0, 0, 0.253);
  border-radius: 0.1875rem;
  background-color: #fff;
  font-size: 12px;
}

.finder-pw-btn {
  width: 100%;
  height: 46px;
  border: none;
  border-radius: 0.1875rem;
  background-color: #1a73e8;
  color: white;
  margin-top: 20px;
  font-weight: bold;
  cursor: pointer;
}

.left-info {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.info-title {
  font-size: 16px;
  font-weight: bold;
}

.profile-frame {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  background: #f0f0f0;
  width: 265px;
  height: 40px;
  border-radius: 10px;
  color: #a5a5a9;
}
</style>
